<template>
  <div class="my-data container-fluid">
    <div class="row shadow-sm sticky-top reset-title">
      <div class="col">
        <button
          class="btn btn-Secondary"
          type="button"
          :disabled="Loading"
          @click.stop="handleRefresh"
        >
          <template v-if="Loading">
            <span class="spinner-grow spinner-grow-sm" role="status" aria-hidden="true"></span>
            <span class="sr-only">Loading...</span>
          </template>
          <template v-else>
            刷新数据
            <svg
              class="bi bi-arrow-repeat"
              width="1em"
              height="1em"
              viewBox="0 0 16 16"
              fill="currentColor"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                d="M2.854 7.146a.5.5 0 0 0-.708 0l-2 2a.5.5 0 1 0 .708.708L2.5 8.207l1.646 1.647a.5.5 0 0 0 .708-.708l-2-2zm13-1a.5.5 0 0 0-.708 0L13.5 7.793l-1.646-1.647a.5.5 0 0 0-.708.708l2 2a.5.5 0 0 0 .708 0l2-2a.5.5 0 0 0 0-.708z"
              />
              <path
                fill-rule="evenodd"
                d="M8 3a4.995 4.995 0 0 0-4.192 2.273.5.5 0 0 1-.837-.546A6 6 0 0 1 14 8a.5.5 0 0 1-1.001 0 5 5 0 0 0-5-5zM2.5 7.5A.5.5 0 0 1 3 8a5 5 0 0 0 9.192 2.727.5.5 0 1 1 .837.546A6 6 0 0 1 2 8a.5.5 0 0 1 .501-.5z"
              />
            </svg>
          </template>
        </button>
      </div>
    </div>
    <div class="row mt-2">
      <div class="col-md-6 draw-item" id="category">分类数据</div>
      <div class="col-md-6 draw-item" id="create">递增曲线</div>
      <div class="col-md-6 draw-item" id="likes">博客收藏排行</div>
      <div class="col-md-6 draw-item" id="views">博客浏览排行</div>
      <div class="col-md-6 draw-item" id="user-add">用户发布排行</div>
    </div>
  </div>
</template>

<script>
import draw from "../utils-admin/mixindraw";
import moment from "moment";
export default {
  mixins: [draw],
  data() {
    return {
      Loading: false
    };
  },
  activated() {
    this.getCategoryData();
    this.getStatistics();
  },
  methods: {
    async handleRefresh() {
      if (this.Loading) return;
      this.Loading = true;
      let { ok } = await this.$store.dispatch("refreshStatistics");
      if (ok) location.reload();
    },
    async getCategoryData() {
      //category
      this.showLoading(["#category"]);
      let data = this.$store.state.blog.drawCategory;
      if (!data.length) {
        data = await this.$store.dispatch("getTypes_num");
      }
      let category = data.map(v => v.name);
      let category_item_num = data.map(v => v.num);
      let category_item_like = data.map(v => v.likes_num);
      let option = {
        title: {
          text: "分类数据",
          textStyle: {
            fontSize: 14
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["博客数量", "收藏数量"]
        },
        xAxis: {
          type: "category",
          data: category
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "博客数量",
            type: "bar",
            stack: "总量",
            label: {
              show: true
            },
            data: category_item_num
          },
          {
            name: "收藏数量",
            type: "bar",
            stack: "总量",
            label: {
              show: true
            },
            data: category_item_like
          }
        ]
      };
      this.MyDraw(option, "#category");
    },
    // views likes createAt
    async getStatistics() {
      this.showLoading(["#views", "#likes", "#user-add", "create"]);
      let data = this.$store.state.blog.statistics;
      if (!Object.keys(data).length) {
        data = await this.$store.dispatch("getStatistics");
      }
      let { likes, views, createdAt, counts: user_count } = data;
      this.setLikesOptions(likes);
      this.setViewsOptions(views);
      this.setUserCountOptions(user_count);
      this.setCtreateAtOptions(createdAt);
    },
    setViewsOptions(preOption) {
      let category = preOption.map(v => v.title);
      let category_item_num = preOption.map(v => v.views);
      let option = {
        title: {
          text: "博客浏览排行",
          textStyle: {
            fontSize: 14
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["观看数量"]
        },
        xAxis: {
          type: "category",
          show: false,
          data: category
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "观看数量",
            type: "bar",
            label: {
              show: true
            },
            data: category_item_num
          }
        ]
      };
      this.MyDraw(option, "#views");
    },
    setLikesOptions(preOption) {
      let category = preOption.map(v => v.title);
      let category_item_num = preOption.map(v => v.likes_length);
      let option = {
        title: {
          text: "博客收藏排行",
          textStyle: {
            fontSize: 14
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["收藏数量"]
        },
        xAxis: {
          type: "category",
          show: false,
          data: category
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "收藏数量",
            type: "bar",
            label: {
              show: true
            },
            data: category_item_num
          }
        ]
      };
      this.MyDraw(option, "#likes");
    },
    setUserCountOptions(preOption) {
      let category = preOption.map(v => v.username);
      let category_item_num = preOption.map(v => v.blog_count);
      let option = {
        title: {
          text: "发布数量排行",
          textStyle: {
            fontSize: 14
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["发布数量"]
        },
        xAxis: {
          type: "category",
          show: false,
          data: category
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "发布数量",
            type: "bar",
            label: {
              show: true
            },
            data: category_item_num
          }
        ]
      };
      this.MyDraw(option, "#user-add");
    },
    setCtreateAtOptions(preOption) {
      // {
      //   date:value
      // }
      let dateForm = {};
      let dateFormCol = {};
      for (let i = 0; i < preOption.length; i++) {
        const item = preOption[i];
        let date = moment(item.createdAt).format("YYYY/MM/DD");
        dateForm[date]
          ? (dateForm[date] += 1)
          : (dateForm[date] = (Object.values(dateForm).pop() || 0) + 1);

        dateFormCol[date] ? (dateFormCol[date] += 1) : (dateFormCol[date] = 1);
      }
      var option = {
        title: {
          text: "博客递增曲线",
          textStyle: {
            fontSize: 14
          }
        },
        legend: {
          data: ["总数量", "新增数量"]
        },
        tooltip: {
          trigger: "axis"
        },
        xAxis: {
          type: "time",
          splitLine: {
            show: true
          }
        },
        yAxis: {
          type: "value",
          interval: 1,
          splitLine: {
            show: true
          }
        },
        series: [
          {
            name: "篇 总数量",
            type: "line",
            data: Object.entries(dateForm)
          },
          {
            name: "篇 新增数量",
            type: "bar",
            data: Object.entries(dateFormCol)
          }
        ]
      };
      this.MyDraw(option, "#create");
    }
  }
};
</script>

<style lang="scss" scoped>
.my-data {
  width: 100%;
  overflow: hidden;
  overflow-y: auto;
  height: calc(100vh - 56px);
  .reset-title {
    background-color: #fff;
    z-index: 1;
  }
  .draw-item {
    min-height: 250px;
    min-width: 250px;
  }
}
</style>